@import url(https://fonts.googleapis.com/css?family=Open+Sans);
.header {
    margin-top: .5rem;
    padding-left: 10px;
  }
  
  .header ul li {
    list-style: none;
    margin: 0;
    padding: .5rem 1rem;
    border-radius: 3px;
  }
  
  .header ul li:hover {
    background-color: #444;
  }
  
  .header a:hover {
    text-decoration: none;
  }
  
  .header h3 a {
    color: white;
  }

  .header ul li a:hover {
      color:#fff;
  }
  
  .header h3 a:hover {
    color: #00bc8c;
  }





@keyframes lds-double-ring {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes lds-double-ring {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  @keyframes lds-double-ring_reverse {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(-360deg);
      transform: rotate(-360deg);
    }
  }
  @-webkit-keyframes lds-double-ring_reverse {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(-360deg);
      transform: rotate(-360deg);
    }
  }
  .lds-double-ring {
    position: relative;
  }
  .lds-double-ring div {
    box-sizing: border-box;
  }
  .lds-double-ring > div {
    position: absolute;
    width: 168px;
    height: 168px;
    top: 16px;
    left: 16px;
    border-radius: 50%;
    border: 8px solid #000;
    border-color: #1189ec transparent #1189ec transparent;
    -webkit-animation: lds-double-ring 2.9s linear infinite;
    animation: lds-double-ring 2.9s linear infinite;
  }
  .lds-double-ring > div:nth-child(2),
  .lds-double-ring > div:nth-child(4) {
    width: 148px;
    height: 148px;
    top: 26px;
    left: 26px;
    -webkit-animation: lds-double-ring_reverse 2.9s linear infinite;
    animation: lds-double-ring_reverse 2.9s linear infinite;
  }
  .lds-double-ring > div:nth-child(2) {
    border-color: transparent #fd5623 transparent #fd5623;
  }
  .lds-double-ring > div:nth-child(3) {
    border-color: transparent;
  }
  .lds-double-ring > div:nth-child(3) div {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  .lds-double-ring > div:nth-child(3) div:before,
  .lds-double-ring > div:nth-child(3) div:after {
    content: "";
    display: block;
    position: absolute;
    width: 8px;
    height: 8px;
    top: -8px;
    left: 72px;
    background: #1189ec;
    border-radius: 50%;
    box-shadow: 0 160px 0 0 #1189ec;
  }
  .lds-double-ring > div:nth-child(3) div:after {
    left: -8px;
    top: 72px;
    box-shadow: 160px 0 0 0 #1189ec;
  }
  .lds-double-ring > div:nth-child(4) {
    border-color: transparent;
  }
  .lds-double-ring > div:nth-child(4) div {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  .lds-double-ring > div:nth-child(4) div:before,
  .lds-double-ring > div:nth-child(4) div:after {
    content: "";
    display: block;
    position: absolute;
    width: 8px;
    height: 8px;
    top: -8px;
    left: 62px;
    background: #fd5623;
    border-radius: 50%;
    box-shadow: 0 140px 0 0 #fd5623;
  }
  .lds-double-ring > div:nth-child(4) div:after {
    left: -8px;
    top: 62px;
    box-shadow: 140px 0 0 0 #fd5623;
  }
  .lds-double-ring {
    width: 73px !important;
    height: 73px !important;
    -webkit-transform: translate(-36.5px, -36.5px) scale(0.365) translate(36.5px, 36.5px);
    transform: translate(-36.5px, -36.5px) scale(0.365) translate(36.5px, 36.5px);
  }
.error-indicator {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #c78f22;
  }
  
  .error-indicator img {
    margin-bottom: 1rem;
  }
  
  .error-indicator .boom {
    font-size: 1.7rem;
  }
.random-planet {
    display: flex;
    padding: 1rem;
}

.spinner {
    margin-left: 100px;
}

.random-planet .planet-image {
    width: 150px;
    height: 200;
    border-radius: 10px;
    margin-right: 1px;
}

.random-planet .list-group {
    margin: 1rem 0 0 1rem;
}

.random-planet .planet-name {
    margin: 0.25rem 0 0 1rem;
}
  
.random-planet .list-group-item {
    padding: 0.25rem;
}
  
.random-planet .list-group-item .term {
    margin-right: 1rem;
    color: #4ca08b;
}

.item-details {
    display: flex;
    flex-direction: row;
    padding: 1rem;
  }
  
  .item-details .item-image {
    width: 30%;
    height: 30%;
    border-radius: 10px;
  }
  
  .item-details .list-group-item {
    padding: .25rem;
  }
  
  .item-details .list-group-item .term{
    margin-right: .5rem;
  }

  

.item-list {
    margin-bottom: 50px;
}

.item-list .list-group-item {
    cursor: pointer;
}

.item-list .list-group-item:hover {
    background-color: #444;
}
.app {
    margin: 0 20%;
}

.toggle-planet {
    margin: 25px;
}
